const {round} = Math;
const internals = {
  formatDecimals: (num) => round(num * 100) / 100
};
export default class FeColorMatrixInput {
  constructor(config = {}) {
    Object.assign(this.config = {}, {
      el: null
    }, config);
  }
  setValue([r, g, b, a = 1]) {
    this.config.el.value = this._getColorMatrixValue(r, g, b, a).replace(/$\s+/gm, "\n");
    return this;
  }
  _getColorMatrixValue(...channels) {
    return `${internals.formatDecimals(channels[0])}	0	0	0	0
            0	${internals.formatDecimals(channels[1])}	0	0	0
            0	0	${internals.formatDecimals(channels[2])}	0	0
            0	0	0	${internals.formatDecimals(channels[3])}	0`;
  }
}
